html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'Helvetica Neue', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-y: hidden;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}

#glassImg {
	position: absolute;
	top: -5000px;
	left: 0;
	width: 30%;
	z-index: 5000;
}

#btn-show-glasses {
	position: absolute;
	bottom: 40px;
	right: 20px;
	z-index: 5500;
}



