.photoframe {
	width: 100%;
	min-height: 400px;
	height: 400px;
	text-align: center;
	margin-top: 10px;
}

.frame {
	margin: 0 auto;
	min-height: 400px;
	text-align: center;
}

video, canvas {
	left: calc(50% - 200px);
	width: 400px;
	height: 400px;
	position: absolute;
}




